import Ajv from 'ajv';
import { JSONSchemaBridge } from 'uniforms-bridge-json-schema';
var ajv = new Ajv({
  allErrors: true,
  useDefaults: true
});

function createValidator(schema) {
  var validator = ajv.compile(schema); // tslint:disable-next-line:no-any

  return function (model) {
    validator(model);

    if (validator.errors && validator.errors.length) {
      throw {
        details: validator.errors
      };
    }
  };
}

function makeUniformsSchema(jsonSchema) {
  var fullSchema = Object.assign({
    type: 'object'
  }, jsonSchema);
  return new JSONSchemaBridge(fullSchema, createValidator(fullSchema));
}

export default makeUniformsSchema;